import LoginPage from "./components/page/Login.vue";
import { useRoute, useRouter } from "vue-router";
export default {
  name: "App",
  data() {
    return {
      notLogin: !sessionStorage.getItem("is-Login") ? true : false,
      isLogin: !sessionStorage.getItem("is-Login") ? false : true,
