    const router = useRouter();
    await router.isReady();
    if (route.path.split("/")[1] == "admin") {
      console.log("here");
      this.adminUrl = true;
    } else {
      console.log("web");
    }
    this.initializeSelect2();
    // sessionStorage.clear()
  },
